.pcontainer{
    display: flex;
  margin-top: 50px;
  background-color: rgb(201, 246, 201);
 .box1{
    width: 50%;
    h1{
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        margin-left: 200px;
        font-size: 50px;
        margin-top: 100px;
        opacity: 0.7;
        letter-spacing: 1.5px;
        word-spacing: 5px;
        b{
            color: red;
        }
    }
 }
 .box2{
    width: 50%;
    img{
        height: 300px;
    }
 }
}

.projects{
    margin-top: 30px;
display: flex;
justify-content: center;
    padding: 20px;
    .maincard{
      
       display: grid;
       width: 70%;

   grid-template-columns: repeat(3, 1fr);
   grid-template-rows: repeat(2 , 1fr);
   gap: 1em;
   padding: 10px;

    }
    .cards{
        height: 200px;
        width: 100%;
        background-color: rgb(198, 247, 198);
        border-radius: 20px;
        display: flex;
        flex-direction: column;
        
        align-items: center;
        .upper{
            background-color: black;
            height: 20px;
            margin-bottom: 30px;
            width: 100%;
        }
        h1{
            font-size: 15px;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
            opacity: 0.8;
        }
        a{
            margin-top: 20px;
            background-color: black;
            color: white;
            text-decoration: none;
            padding: 10px;
            font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        }
    }
}