$primary-color   : #0A2647;
$nunito  : 'Nunito', sans-serif;

.upper-bar{
    height: 50px;
    display: flex;
    margin-left: 70px;
    margin-top: 20px;
    font-size: 20px;
    opacity: 0.6;
    justify-content: center;
    align-items: center;
   
    font-weight: 600;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  a{
    text-decoration: none;
    color: black;
  }
    p{
        margin-right: 100px;
        letter-spacing: 1px;
        transition: 0.5s;
        cursor: pointer;
        &:hover{
            letter-spacing: 3px;
        }
    }
    h1{
        font-family: 'Damion', cursive;
        color: red;
        opacity: 1.0;
        margin-left: 8%;
       
    }
    .bar{
        margin-left: 15%;
        display: flex;
        
    }
   
    z-index: 9999;
    position: sticky;
 
}

.mid-bar{
    display: flex;
  
    align-items: center;
    .box1{
       font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        margin-left: 50px;
        width: 50%;
        color: $primary-color;
        h1{
            b{
                font-weight: 800;
                
            }
        }
        h1 {
            color: $primary-color;
            font-weight: 100;
            font-size: 70px;
            font-weight: bold!important;
            cursor: pointer;
           
          }
          
          h1 {
          
            background: linear-gradient(to right, hsl(0, 0%, 30%) 0, hsl(0, 0%, 100%) 10%, hsl(0, 0%, 30%) 20%);
            -webkit-background-clip: text; 
            -webkit-text-fill-color: transparent;
            animation: shine 3s infinite linear;
          }
          
          @keyframes shine {
            0% {
              background-position: 0;
            }
            60% {
              background-position: 600px;
            }
            100% {
              background-position: 600px;
            }
          }


      
    }
.box2{
  
   
  .eye{
    position: relative;
     left: 10px;
     height: 40px;
     width: 30px;
     border-radius: 50%;
     top: 220px;
     left: 230px;
     background-color: #000074;
     overflow: hidden;
    .pupil{
     background-color: white;
     height: 15px;
     width: 15px;
     border-radius: 50%;
     position: absolute;
      
  transition: all 0.1s;
    }
}

.eye2{
    position: relative;
